import { Component, OnInit } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { TranslateService} from '@ngx-translate/core'

@Component({
  selector: 'app-mianapp',
  templateUrl: './mianapp.component.html',
  styleUrls: ['./mianapp.component.scss']
})
export class MianappComponent implements OnInit {
  private  test_url:string = 'https://djangoherokupl.herokuapp.com/api';
  public users
  constructor(private http:HttpClient,public translate:TranslateService) { 
    translate.addLangs(['en','pl'])
    translate.setDefaultLang('en')
    translate.use('en')
  }
  public ngOnInit(): void {
    this.Get_users()
  }
  private Get_users()  {
    return this.http.get(this.test_url).subscribe(users=>{
        this.users=users
        console.log(this.users)
    })

  }

}
